<template>
  <div class="hello">
    <p style="margin: auto">
      1.  ETH<br/>
      2.  TG /thanks +<br/>
      3.  xxx.io/  <br/>
    </p>
    <input v-model="address" placeholder="eth" />
    <div><button @click="generateAddress()">eth</button></div>
  </div>
</template>

<script>
import md5 from 'md5';
export default {
  name: 'page',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      address: localStorage.getItem("address")
    }
  },
  methods: {
    generateAddress () {
      localStorage.setItem("address", md5(this.address))
      this.$router.push({path: '/eth', query: this.address})
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



